/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */
.wp-block-formello-form fieldset {
  padding: 10px;
  border: 1px solid black;
}
.wp-block-formello-form select {
  max-width: inherit;
}
.wp-block-formello-form input:disabled, .wp-block-formello-form input.disabled, .wp-block-formello-form select:disabled, .wp-block-formello-form select.disabled, .wp-block-formello-form textarea:disabled, .wp-block-formello-form textarea.disabled {
  background-color: #fff;
  border: 1px solid #7e8993;
  opacity: 1;
  border-radius: 0;
  line-height: normal;
}
.wp-block-formello-form input:disabled[type=radio] {
  border: 1px solid #7e8993;
  border-radius: 50%;
}
.wp-block-formello-form .formello {
  padding: 0;
}
.wp-block-formello-form .formello small {
  padding: 0;
}

.formello-checkbox input {
  margin: 0;
}

.formello-hidden {
  display: flex;
  align-items: center;
}

.wp-block-formello-button .button-span {
  cursor: pointer;
  font-size: 14px;
  padding: 5px 10px;
  background: #abb8c3;
  color: #fff;
  border: 1px solid transparent;
  align-self: flex-start;
}
.wp-block-formello-button .button-span.center {
  align-self: center;
}
.wp-block-formello-button .button-span.right {
  align-self: flex-end;
}
.wp-block-formello-button .button-span.wide {
  width: 100%;
}

.wp-block-formello-button.wide .button-span {
  width: 100%;
  text-align: center;
}

.formello-group.grouped input {
  border-right: 0;
}

.formello-group div {
  display: flex;
  flex: 0 0 auto;
}
.formello-group div [data-type="formello/button"] {
  flex: 0 1 auto;
}
.formello-group div.formello {
  padding: 0;
  margin: 0;
}
.formello-group div.formello label {
  display: none;
}
.formello-group div.formello small {
  display: none;
}
.formello-group div .editor-styles-wrapper [data-block] {
  margin: 0 !important;
}
.formello-group div .block-editor-block-list__layout .wp-block {
  margin: 0;
}

.formello-urlinput input {
  width: 100% !important;
  border-radius: 2px !important;
  border: 1px solid #757575 !important;
}

.formello-group div.ld-spin {
  display: inline-block;
}